:root {
  --color-darkest: #000;
  --color-darker-2: #222;
  --color-darker-1: #2f2f2f;
  --color-dark: #666;
  --color-normal: #888;
  --color-light: #aaa;
  --color-lighter-1: #ccc;
  --color-lighter-2: #eee;
  --color-lightest: #fff;
  --color-link: #55f;
}

* {
  margin: 0;
  padding: 0;
  font-family: poppins, Poppins, Arial, sans-serif;
}
body, .app-main-container {
  background-color: var(--color-darker-1);
  color: var(--color-lighter-1);
}
.app-sub-container {
  display: flex;
  margin-top: 1em;
  gap: 1em;
}
.app-sub-item {
  text-align: center;
}

.app-others-container {
  flex: 1 1 25%;
}

@media only screen and (max-width: 1000px) {
  .app-sub-container {
    flex-direction: column-reverse;
  }
  .app-sub-item {
    flex: 1 1 100%;
  }
}
