
.script-main-container{
border: 2px solid var(--color-darker-1);
padding: 2em 2em;
display: flex;
flex-direction: column;
align-items: center;
gap: 1em;
}

.script-title{
    font-size: 2em;
    flex: 1 1 20%;
}
.script-description{
    font-size: 1em;
    flex: 1 1 60%;
    color: var(--color-normal);
}

.script-main-container .button-main-container{
    flex: 1 1 20%;
    padding: 1em;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
}